import React from 'react';
import { Container, Table } from 'react-bootstrap';
import { useDispatch, useSelector } from "react-redux";
import { addCount, subCount } from "../../store.js";
import { useNavigate } from 'react-router-dom';

import './Cart.css';

function Cart() {
    let cart = useSelector(state => state.cart);
    let dispatch = useDispatch()
    const navigate = useNavigate();

    return (
        <div className='cart'>
            <Container>
                <h1>Cart</h1>

                {cart.length === 0 ? (
                    <p className='empty'>Your cart is empty.</p>
                ) : (
                    <Table>
                        <tbody>
                            {cart.map((v, i) => (
                                <tr key={i}>
                                    <td className='img'><img src={process.env.PUBLIC_URL + v.imgUrl} width="50%" onClick={()=> navigate('/detail/'+ v.id)} alt='item'/></td>
                                    <td className='item'>{v.item}</td>
                                    <td className='addCount'><button onClick={() =>
                                        dispatch(subCount(i))
                                    }>-</button></td>
                                    <td className='amount'>{v.amount}</td>
                                    <td className='addCount'><button onClick={() =>
                                        dispatch(addCount(i))
                                    }>+</button></td>
                                </tr>
                            ))}
                        </tbody>
                        {cart.length > 0 && (
                            <tfoot>
                                <tr className='total'>
                                    <td colSpan="2" style={{ textAlign: 'right', fontWeight: 'bold' }}>                                        
                                        {
                                            cart.reduce((total, item) => total + (item.price * item.amount), 0).toLocaleString()
                                        }&emsp;+&emsp;880 <span className='fee'>(Shipping)</span>&emsp;=&emsp;</td>
                                    <td colSpan="3" style={{ fontWeight: 'bold' }}>
                                        Total&ensp;{
                                            (cart.reduce((total, item) => total + (item.price * item.amount), 0) + 880).toLocaleString()
                                        }
                                    </td>
                                </tr>
                                <tr>
                                    <td colSpan="5" style={{ textAlign: 'right' }}>
                                        <button className='order'>
                                            Order Now
                                        </button>
                                    </td>
                                </tr>
                            </tfoot>
                        )}
                    </Table>
                )}
            </Container>
        </div>
    );
}

export default Cart;