import React, { useState } from 'react';
import { Container, Row, Col, Nav } from 'react-bootstrap'
import { useNavigate, useParams } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { addItem } from '../../store';

import './Detail.css';
import TabContent from '../TabContent/TabContent';

const Detail = (props) => {
  let { id } = useParams();
  const { items } = props;
  let [tab, setTab] = useState(0);
  let dispatch = useDispatch();
  const navigate = useNavigate();

  const moveCart = () => {
    dispatch(addItem({ id: items[id].id, imgUrl: items[id].imgUrl, item: items[id].title, price: items[id].price, amount: 1 }));
    navigate('/cart');
  }

  

  return (
    <div className='detail'>
      <Container>
        <Row>
          <Col md={6} className='text-center'>
            <img src={process.env.PUBLIC_URL + items[id].imgUrl} width="60%" />
          </Col>
          <Col md={6} className='position-relative'>
            <h4>{items[id].title}</h4>
            <p>{items[id].price.toLocaleString()}<span> (tax included)</span></p>
            <div className='btns'>
              <button onClick={moveCart}>Add to cart</button>
              <button><i class="fa-regular fa-heart"></i></button>
            </div>
          </Col>
        </Row>
        <Nav variant="tabs" defaultActiveKey="link0" className='tab'>
          <Nav.Item>
            <Nav.Link onClick={() => { setTab(0) }} eventKey="link0">Info</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link onClick={() => { setTab(1) }} eventKey="link1">Size</Nav.Link>
          </Nav.Item>
          <Nav.Item>
            <Nav.Link onClick={() => { setTab(2) }} eventKey="link2">Material</Nav.Link>
          </Nav.Item>
        </Nav>
        <TabContent tab={tab} />
      </Container>
    </div>
  )
}

export default Detail;