import React from 'react';
import './Nav.css';
import { Container, Nav, Navbar, NavDropdown } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

const Navigation = () => {
    const navigate = useNavigate();
    return (
        <Navbar collapseOnSelect expand="lg" bg="dark">
            <Container>
                <Navbar.Brand onClick={()=>{
                            navigate('/');
                        }}>Home</Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link onClick={()=>{
                            navigate('/product');
                        }}>All Items</Nav.Link>
                        <NavDropdown title="Search by Anime" id="collapsible-nav-dropdown">
                            <NavDropdown.Item>ONE PIECE</NavDropdown.Item>
                            <NavDropdown.Item>My Hero Academia</NavDropdown.Item>
                            <NavDropdown.Item>Jujutsu Kaisen</NavDropdown.Item>
                            <NavDropdown.Item>MASHLE: MAGIC AND MUSCLES</NavDropdown.Item>
                            <NavDropdown.Item>SAKAMOTO DAYS</NavDropdown.Item>
                            <NavDropdown.Item>Haikyu!!</NavDropdown.Item>
                            <NavDropdown.Item>Demon Slayer: Kimetsu no Yaiba</NavDropdown.Item>
                            <NavDropdown.Item>Chainsaw Man</NavDropdown.Item>
                            <NavDropdown.Item>Rurouni Kenshin: Meiji Kenkaku Romantan</NavDropdown.Item>
                            <NavDropdown.Item>DRAGON BALL</NavDropdown.Item>
                            <NavDropdown.Item>Gintama</NavDropdown.Item>
                            <NavDropdown.Item>JoJo's Bizarre Adventure</NavDropdown.Item>
                            <NavDropdown.Item>Golden Kamuy</NavDropdown.Item>
                            <NavDropdown.Item>World Trigger</NavDropdown.Item>
                            <NavDropdown.Item>&emsp;jumpSQ</NavDropdown.Item>
                            <NavDropdown.Item>&emsp;Shonen Jump+</NavDropdown.Item>
                            <NavDropdown.Item>&emsp;Others</NavDropdown.Item>
                        </NavDropdown>
                        <Nav.Link>User Guide</Nav.Link>
                        <Nav.Link>FAQ</Nav.Link>
                    </Nav>
                    <Nav>
                        <Nav.Link><i className="fa-solid fa-magnifying-glass"></i></Nav.Link>
                        <Nav.Link onClick={()=>{
                            navigate('/login');
                        }}><i className="fa-solid fa-user"></i></Nav.Link>
                        <Nav.Link onClick={()=>{
                            navigate('/cart');
                        }}><i className="fa-solid fa-cart-shopping"></i></Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}

export default Navigation;