import { React, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Pagination, Navigation } from 'swiper/modules';
import { Row, Col, Container } from 'react-bootstrap';
import items from '../../data';
import { useNavigate } from 'react-router-dom';

import './Home.css';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

const Home = () => {
  let [item] = useState(items);
  const [count, setCount] = useState({
    recommended: 4,
    new: 4,
    restocked: 4,
    birthday: 4,
  });

  const handleMore = (category) => {
    const categoryMap = {
      recommended: recommendedItem,
      new: newItem,
      restocked: restockedItem,
      birthday: birthdayItem
    };

    const currentList = categoryMap[category];
    const currentCount = count[category];

    if (currentCount >= currentList.length) {
      alert("No more items.");
      return;
    }

    setCount(prev => ({
      ...prev,
      [category]: prev[category] + 4
    }));
  };

  const navigate = useNavigate();

  let recommendedItem = [...item].filter(a => a.category === "recommended");
  let newItem = [...item].filter(a => a.category === "new");
  let restockedItem = [...item].filter(a => a.category === "restocked");
  let birthdayItem = [...item].filter(a => a.category === "birthday");

  return (
    <div className='home'>
      <div className='slider'>
        <h1>NEWS</h1>
        <Swiper
          slidesPerView={1}
          spaceBetween={0}
          loop={true}
          pagination={{
            clickable: true,
          }}
          navigation={true}
          modules={[Autoplay, Pagination, Navigation]}
          autoplay={{
            delay: 3500,
            disableOnInteraction: false,
          }}
          className="mySwiper sliderSwiper"
        >
          <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/slide1.webp"} alt="slide1"></img></SwiperSlide>
          <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/slide2.webp"} alt="slide2"></img></SwiperSlide>
          <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/slide3.webp"} alt="slide3"></img></SwiperSlide>
          <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/slide4.webp"} alt="slide4"></img></SwiperSlide>
          <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/slide5.webp"} alt="slide5"></img></SwiperSlide>
          <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/slide6.webp"} alt="slide6"></img></SwiperSlide>
          <SwiperSlide><img src={process.env.PUBLIC_URL + "/img/slide7.webp"} alt="slide7"></img></SwiperSlide>
        </Swiper>
        <Row className='bn'>
          <Col md={4}><img src={process.env.PUBLIC_URL + "/img/bn1.webp"} alt="bn1"></img></Col>
          <Col md={4}><img src={process.env.PUBLIC_URL + "/img/bn2.webp"} alt="bn2"></img></Col>
          <Col md={4}><img src={process.env.PUBLIC_URL + "/img/bn3.webp"} alt="bn3"></img></Col>
        </Row>
      </div>
      <div className='notice'>
        <h3>Notice</h3>
        <table>
          <tbody>
            <tr>
              <th>2024.10.29</th>
              <td></td>
            </tr>
            <tr>
              <th>2024.01.05</th>
              <td>6</td>
            </tr>
            <tr>
              <th>2023.10.12</th>
              <td></td>
            </tr>
          </tbody>
        </table>
      </div>
      <Container className='recommended items'>
        <h1>Recommended Items</h1>
        <Row>
          {
            recommendedItem.slice(0, count.recommended).map((x, i) =>
              <Col sm={6} xl={3} key={i}>
                <img src={process.env.PUBLIC_URL + x.imgUrl} width="80%" alt={"recommended" + (i + 1)} onClick={()=> navigate('/detail/'+ x.id)} />
                <h4>{x.title}</h4>
                <p>{x.price.toLocaleString()}<span> (tax included)</span></p>
              </Col>
            )
          }
        </Row>
        <button onClick={() => handleMore('recommended')}>more</button>
      </Container>
      <Container className='new items'>
        <h1>New Items</h1>
        <Row>
          {
            newItem.slice(0, count.new).map((x, i) =>
              <Col sm={6} xl={3} key={i}>
                <img src={process.env.PUBLIC_URL + x.imgUrl} width="80%" alt={"new" + (i + 1)} onClick={()=> navigate('/detail/'+ x.id)} />
                <h4>{x.title}</h4>
                <p>{x.price.toLocaleString()}<span> (tax included)</span></p>
              </Col>
            )
          }
        </Row>
        <button onClick={() => handleMore('new')}>more</button>
      </Container>
      <Container className='restocked items'>
        <h1>Restocked Items</h1>
        <Row>
          {
            restockedItem.slice(0, count.restocked).map((x, i) =>
              <Col sm={6} xl={3} key={i}>
                <img src={process.env.PUBLIC_URL + x.imgUrl} width="80%" alt={"restocked" + (i + 1)} onClick={()=> navigate('/detail/'+ x.id)} />
                <h4>{x.title}</h4>
                <p>{x.price.toLocaleString()}<span> (tax included)</span></p>
              </Col>
            )
          }
        </Row>
        <button onClick={() => handleMore('restocked')}>more</button>
      </Container>
      <Container className='birthday items'>
        <h1>Birthday Items</h1>
        <Row>
          {
            birthdayItem.slice(0, count.birthday).map((x, i) =>
              <Col sm={6} xl={3} key={i}>
                <img src={process.env.PUBLIC_URL + x.imgUrl} width="80%" alt={"birthday" + (i + 1)} onClick={()=> navigate('/detail/'+ x.id)} />
                <h4>{x.title}</h4>
                <p>{x.price.toLocaleString()}<span> (tax included)</span></p>
              </Col>
            )
          }
        </Row>
        <button onClick={() => handleMore('birthday')}>more</button>
      </Container>
    </div>
  )
}

export default Home