import React from 'react';

import './Login.css';

const Login = () => {
    return (
        <div className='login'>
            <h1>Log in</h1>
            <input type="text" placeholder="Enter your email" />
            <br />
            <div>
                <input type="text" placeholder="Enter your password" />
                <br />
                <p>Forgot password?</p>
            </div>
            <br />
            <button>Log in</button>
            <br />
            <p>Not a member? <span>Create an account</span></p>
        </div>
    )
}

export default Login