import { configureStore, createSlice } from '@reduxjs/toolkit'

let cart = createSlice({
    name: 'cart',
    initialState: [],
    reducers: {
        addCount(state, action) {
            let idx = action.payload;
            ++state[idx].amount;
        },
        subCount(state, action) {
            let idx = action.payload;

            if (state[idx].amount > 1) {
                --state[idx].amount;
            } else {
                state.splice(idx, 1);
            }
        },
        addItem(state, action) {
            let incomingItem = action.payload;
            let found = state.find((v) => v.item === incomingItem.item);

            if (found) {
                found.amount += 1;
            } else {
                state.push({ ...incomingItem, amount: 1 });
            }
        },
    }
})

export let { addCount, subCount, addItem } = cart.actions

export default configureStore({
    reducer: {
        cart: cart.reducer
    }
}) 