import React from 'react'
import items from '../../data';
import { Col, Container, Row } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

import './Product.css';

const Product = () => {
  const navigate = useNavigate();

  return (
    <div className='product'>
    <Container className='all items'>
      <h1>All Items</h1>
      <Row>
        {
          items.map((x, i) =>
            <Col sm={6} xl={3} key={i}>
              <img src={process.env.PUBLIC_URL + x.imgUrl} width="80%" alt={"item" + (i + 1)} onClick={()=> navigate('/detail/'+ x.id)} />
              <h4>{x.title}</h4>
              <p>{x.price.toLocaleString()}<span> (tax included)</span></p>
            </Col>
          )
        }
      </Row>
    </Container>
    </div>
  )
}

export default Product;