import React from 'react';

import './TabContent.css';

const TabContent = (props) => {
  let { tab } = props;
console.log(tab)
  if (tab === 0) {
    return <div className='content'>
      (4/1)<br /><br />
      <br />
      <br />
      <br />
      <br />
    </div>
  } else if (tab === 1) {
    return <div className='content'>
      <br /><br />
      180mm130mm65mm<br />
      70mm70mm<br />
    </div>
  } else if (tab === 2) {
    return <div className='content'>
      <br /><br />
      <br />
    </div>
  }
}

export default TabContent;